.grid-images{
    margin: 5px;
	display: grid;
	grid-template: repeat(3,33vw) / repeat(3,30vw);
    justify-content: center;
	grid-gap: 10px;
    grid-auto-rows: 33vw;
}

.box{
    background: #f0050f;
}

#addEmotion{
    position: absolute;
    border-radius: 50%;
    background-color: #cbd9e9;
    border: 1px solid #5281b7;
}
